import React, { createContext, useContext, useState } from 'react';
import axios from 'axios';
import { useEffect } from 'react';

export const AuthContext = createContext();

export const useAuth = () => useContext(AuthContext);



export const  AuthProvider = ({children})=> {

    const [user, setUser] = useState(null);
    useEffect(() => {
        // Kim tra v thit lp ngi dng da trn localStorage khi AuthProvider c mount
        const storedUser = JSON.parse(localStorage.getItem('user'));
        if (storedUser) {
          setUser(storedUser);
        }
      }, []);

      const login = async (email, password) => {
        try {
            const response = await axios.get(`http://localhost:9999/users?email=${email}&password=${password}`);
            if (response.data.length > 0) {
                localStorage.setItem('user', JSON.stringify(response.data[0])); // Lu trng thi ng nhp 
                setUser(response.data[0]); // Thit lp ngi dng ng nhp hin ti              
                return true;
            }
            return false;
        } catch (error) {
            console.error("Login failed:", error);
            return false;
        }
    };

    const logout = () => {
        localStorage.removeItem('user'); // Xa trng thi ng nhp
        setUser(null); // Xa thng tin ngi dng
    };

    return (
        <AuthContext.Provider value={{ user, login, logout }}>
            {children}
        </AuthContext.Provider>
    );
}

